import React from 'react';
import i18n from '@dhis2/d2-i18n';
import { PropTypes } from '@dhis2/prop-types';
import { ReactFinalForm, InputFieldFF, SwitchFieldFF, Box } from '@dhis2/ui';
import { useDataQuery } from '@dhis2/app-runtime';
import { getJobTypeParameters, getParameterEndpoint, getStringValue } from './selectors';
import UnlabeledOptionsField from './UnlabeledOptionsField';
import SkipTableTypesField from './SkipTableTypesField';
import LabeledOptionsField from './LabeledOptionsField';
import styles from './ParameterFields.module.css';
const {
  Field
} = ReactFinalForm; // The key under which the parameters will be sent to the backend

const FIELD_NAME = 'jobParameters';
const query = {
  jobTypes: {
    resource: 'jobConfigurations/jobTypes'
  }
}; // Renders all parameters for a given jobtype

const ParameterFields = ({
  jobType
}) => {
  const {
    loading,
    error,
    data
  } = useDataQuery(query);

  if (loading) {
    return null;
  }

  if (error) {
    /**
     * We need the parameters, so throw the error if these
     * can't be loaded.
     */
    throw error;
  }

  const parameters = getJobTypeParameters(data.jobTypes.jobTypes, jobType);

  if (parameters.length === 0) {
    return null;
  } // Map all parameters to the appropriate field types


  const parameterComponents = parameters.map(({
    fieldName,
    name,
    klass,
    relativeApiEndpoint
  }) => {
    const defaultProps = {
      label: fieldName,
      name: `${FIELD_NAME}.${name}`
    };
    const endpoint = getParameterEndpoint(relativeApiEndpoint);
    let parameterComponent = null; // Specific case

    if (name === 'skipTableTypes') {
      return <Box marginTop="16px" key={name}>
                        <SkipTableTypesField {...defaultProps} endpoint={endpoint} />
                    </Box>;
    } // Generic field rendering


    switch (klass) {
      case 'java.lang.String':
        parameterComponent = <Field {...defaultProps} component={InputFieldFF} type="text" />;
        break;

      case 'java.lang.Boolean':
        parameterComponent = <Field {...defaultProps} component={SwitchFieldFF} type="checkbox" />;
        break;

      case 'java.lang.Integer':
        parameterComponent = <Field {...defaultProps} component={InputFieldFF} format={getStringValue} type="number" />;
        break;

      case 'java.util.Set':
        parameterComponent = <UnlabeledOptionsField {...defaultProps} endpoint={endpoint} />;
        break;

      case 'java.util.List':
        parameterComponent = <LabeledOptionsField {...defaultProps} parameterName={name} endpoint={endpoint} />;
        break;
    } // Wrap all components in a Box for spacing


    return <Box marginTop="16px" key={name}>
                    {parameterComponent}
                </Box>;
  });
  return <React.Fragment>
            <header>
                <h4 className={styles.headerTitle}>{i18n.t('Parameters')}</h4>
            </header>
            {parameterComponents}
        </React.Fragment>;
};

const {
  string
} = PropTypes;
ParameterFields.propTypes = {
  jobType: string.isRequired
};
export default ParameterFields;